const initialState = {
   
  	slideshow: {number: 0, dropdown: false},
	otherState: {state: false}
  
};

const enter = (state = initialState, action) => {

    switch (action.type) {
      case "UPDATE_SLIDE_NUMBER":

	return {...state, slideshow: {number: action.number, dropdown: state.slideshow.dropdown}};
	case "UPDATE_DROPDOWN_STATUS":

	return {...state, slideshow: {number: state.slideshow.number, dropdown: action.dropdown}};

      default:
        return state;
    }
};

export default enter;
