import React, {Component} from "react";
import "./components.css";
import { Link } from "gatsby";
import Footer from "./footer";
import { connect } from "react-redux";

const mapStateToProps = state => {

    return {number: state.slideshow.number, dropdown: state.slideshow.dropdown, loaded: state.slideshow.loaded};

};

class Presentation2 extends Component {

	constructor(props) {

		super(props);
		this.state = {
	
			images: ["0.jpg", "1.jpg", "2.jpg", "3.jpg", "4.jpg", "5.jpg", "6.jpg"],
			number: false,
			yellow: false,
			dropdown: false

		}

		this.changePicture = this.changePicture.bind(this);
		this.dotContainer = React.createRef();
		this.startInterval = this.startInterval.bind(this);
		this.openMenu = this.openMenu.bind(this);

	}

	changePicture(e) {

		let target = e.target;
		
		clearInterval(this.intervalId);
		
		for (let item of this.dotContainer.current.children) {

			if (Number(item.id) === Number(target.id)) {

				item.className = "active-dot";
	
			}

			else {

				item.className = "dot";

			}

		}

		this.setState({number: Number(target.id)});

		this.startInterval();
		



	}

	startInterval() {

		
	 this.intervalId = setInterval(() => {
		
		let num = this.state.number + 1;

		if (this.state.number === this.state.images.length -1) {

			num = 0;

		}
	

		this.props.dispatch({type: "UPDATE_SLIDE_NUMBER", number: num});
		for (let item of this.dotContainer.current.children) {

			let num2 = num;

			if (Number(item.id) === num2) {

				item.className = "active-dot";
	
			}

			else {

				item.className = "dot";

			}

		}
		
    		this.setState({number: num});
	
    	 }, 5000);



	}

	static getDerivedStateFromProps(nextProps, prevState) {

        if (prevState.number !== nextProps.number || prevState.dropdown !== nextProps.dropdown || prevState.loaded !== nextProps.loaded) {

            return {

                		number: nextProps.number,
				dropdown: nextProps.dropdown,
				loaded: nextProps.loaded

            }

        }
	
        else {

            return null;

        }

    }
	componentDidMount() {

		this.startInterval();
		this.props.dispatch({type: "UPDATE_DROPDOWN_STATUS", dropdown: false});
		this.props.dispatch({type: "LOADED", loaded: true});
		
		for (let item of this.dotContainer.current.children) {

			let num2 = this.state.number;

			if (Number(item.id) === num2) {

				item.className = "active-dot";
	
			}

			else {

				item.className = "dot";

			}

		}

		window.setTimeout(() => {

			this.setState({yellow: true});


		}, 1000);
  
	}

	componentWillUnmount() {

		clearInterval(this.intervalId);
		

	}

	openMenu() {

		if (this.state.dropdown) {

			this.props.dispatch({type: "UPDATE_DROPDOWN_STATUS", dropdown: false});

		}
	
		else {

			this.props.dispatch({type: "UPDATE_DROPDOWN_STATUS", dropdown: true});

		}

	}


	render() {

		let yellow;
		let text;
		let dropdownHeight;
		let menucolor;

		if (this.state.yellow) {

			yellow = {height: "40%"};
			text = {opacity: 1};
		}

		if (this.state.dropdown) {

			dropdownHeight = {opacity: 1, visibility: "visible"};
			text = {opacity: 1, transform: "translateY(150px)"};
			yellow = {height: "0%"};
			menucolor = {color: "white"};



		}

		if (!this.state.dropdown) {

			menucolor = {position: "absolute"};

		}

		return (  <div><div className="header">
	   <div className="info"><div className="logo-container">
			<Link to="/" className="logo">IKMI TUZLA</Link></div><i  style={menucolor} onClick={this.openMenu} className="fas fa-bars menu"></i>
		<div style={dropdownHeight} className="drop-menu">
			<nav className="links">
			<Link to="/termini">Termini</Link>
			</nav>
</div><div className="introduction"><span style={text}>{this.props.title}</span><div style={yellow}className="visual"></div></div></div>
    <div className="slideshow"><div className="sildeshow-image" style={{backgroundImage:`url(/slideshow/${this.state.images[this.state.number]}`}}/> 
	<div ref={this.dotContainer} className="dots">
	<span onClick={this.changePicture} id="0" className="active-dot"></span>
	<span onClick={this.changePicture} id="1" className="dot"></span>
	<span onClick={this.changePicture} id="2" className="dot"></span>
	<span onClick={this.changePicture} id="3" className="dot"></span>
	<span onClick={this.changePicture} id="4" className="dot"></span>
	<span onClick={this.changePicture} id="5" className="dot"></span>
	<span onClick={this.changePicture} id="6" className="dot"></span>
	</div>
	</div>
  </div>{this.props.children}

	<Footer />
 </div>)


	}


}

const Presentation = connect(mapStateToProps)(Presentation2);

export default Presentation;
